const sendForm = document.getElementById('sendForm')
const gallery = document.querySelector('.gallery')

sendForm.addEventListener('submit', (event) => {
  event.preventDefault()
  const searchNameImg = document.getElementById('searchNameImg').value
  let url = `https://api.unsplash.com/search/photos?per_page=10&page=1&query=${searchNameImg}`
  document.getElementById('searchNameImg').value = ''

  makeRequest('GET', url)
    .then(data => renderSearchImg(data.results))
    .then(err => console.log(err))
  }
)

function makeRequest(method, url) {
  const headers = {
    "Authorization": "Client-ID -dkXdY0X4XMvtNfqESKQXwy3ItkXWHCkVeb-T1EAX_Y",
    'Content-Type': 'application/json;charset=utf-8'
  }
  return fetch(url, {
    method: method,
    headers: headers
  })
    .then(response => {
      return response.json()
    })
}

function renderSearchImg (data) {
  document.getElementById('gallery').innerHTML = "";
  let pointerRender = document.getElementById('gallery')
  data.map(el => {

    let img = new Image()
    img.src = el.urls.small
    img.className = 'imgSearch'
    pointerRender.appendChild(img)
  })
}
